@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:ital,wght@0,300;0,400;0,700;1,300&display=swap);
@import url(https://fonts.googleapis.com/css?family=Exo:100,200,400);
@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:700,400,300);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family:'Titilliun web', sans-serif;
}

:root{
  --mainColor: #0e390b;
  --mainColorLight: #257834;
  --secondaryColor: #c5d87f;
  --textColor: #ffffff;
}

header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 140px;
  padding: 0 2rem;
  background-color: #0e390b;
  background-color: var(--mainColor);
  color: #ffffff;
  color: var(--textColor);
  /* z-index: 200; */
  /* grid-area: header; */
}

nav a{
  margin: 0 2rem;
  color: #ffffff;
  color: var(--textColor);
  text-decoration: none;


  /* flex: 100%;

  align-items: center;
  justify-content: flex-end; */
}

nav s:hover{
  color: #c5d87f;
  color: var(--secondaryColor);

}

header .nav-btn{
  padding: 5px;
  cursor:pointer;
  background: transparent;
  border: none;
  outline: none;
  color: #ffffff;
  color: var(--textColor);
  visibility: hidden;
  opacity: 0;
  font-size: 1.8rem;

}
/* .derecha{
  flex: 50%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
} */

/* .izquierda{
  flex: 50%;
  display: flex;
  align-items: center;
  justify-content: left;
  max-height: 80px;
} */

@media only screen and (max-width: 1024px){
  header .nav-btn{
    visibility: visible;
    opacity: 1;
  }
  header nav{
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    grid-gap: 1.5rem;
    gap: 1.5rem;
    background-color: #0e390b;
    background-color: var(--mainColor);
    transition: 1s;
    -webkit-transform: translateY(-100vh);
            transform: translateY(-100vh);
    z-index: 100;
  }
  header .responsive_nav{
    -webkit-transform: none;
            transform: none;
    /* flex: 50%;
    display: flex;
    align-items: center;
    justify-content: flex-end; */
  }

  nav .nav-close-btn{
    position: absolute;
    top: 2rem;
    right: 2rem;
  }
  nav a{
    font-size: 1.5rem;
  }
}




