.grid-container {
  display: grid;
  width: 100%;
  min-height: 15rem;
  grid-gap: 1rem;
  gap: 1rem;
  grid-template-columns: repeat(auto-fill, minmax(20rem, 1fr));
}

.grid-card {
  background: aliceblue;
  overflow: hidden;
  height: 29rem;
  width: 100%;
  position: relative;
  border: 1px solid rgb(20, 20, 20);
}

.card-content {
  padding: 1rem;
}

.card-img {
    display: flex;
    width: 100%;
    height: 17rem;
    object-fit: cover;

}

.card-name {
    font-size: 1.5rem;
}

.card-name-scientific {
    font-size: 1rem;
}

.card-name-scien {
  font-size: 0.8rem;
}

.card-button {
    background-color: lightpink;
  position: absolute;
  bottom: 1rem;
    border: 1px solid darkgrey;
}

.planta-img {
  display: flex;
  width: 900px;
  height: 600px;
  object-fit: cover;
  border: 3px solid rgb(20, 20, 20);
}

span {
  font-size: 1.4rem;
  color: rgb(20, 20, 20);
}

.planta-title {
  color: brown;
}
.popup-img {
    display: flex;
    width: 300px;
    height: 250px;
    object-fit: cover;
}
body{
	margin: 0;
	padding: 0;
	background: #fff;

	color: #fff;
	font-family: Arial;
	font-size: 12px;
}

.login-text {
	color: white;
	font-size: 1.5rem;
	margin: 0 auto;
	max-width: 50%;
	padding: .5rem;
	text-align: center;

  }
.body{
	position: absolute;
	top: -20px;
	left: -20px;
	right: -40px;
	bottom: -60px;
	width: auto;
	height: auto;
	background-image: url(https://images.pexels.com/photos/213727/pexels-photo-213727.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1);
	background-size: cover;
	-webkit-filter: blur(5px);
	z-index: 0;
}

.grad{
	position: absolute;
	top: -20px;
	left: -200px;
	right: -40px;
	bottom: -40px;
	width: auto;
	height: auto;
	background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,rgba(0,0,0,0)), color-stop(100%,rgba(0,0,0,0.65))); /* Chrome,Safari4+ */
	z-index: 1;
	opacity: 0.7;
}

.header{
	position: absolute;
	top: calc(48% - 10px);
	left: calc(76% - 350px);
	z-index: 2;
}

.header div{
	float: left;
	color: #fff;
	font-family: 'Exo', sans-serif;
	font-size: 35px;
	font-weight: 200;
}

.header div span{
	color: #0ca320 !important;
}

.login{
	position: absolute;
	background: rgb(88, 128, 88);
	top: calc(50% - 100px);
	left: calc(30% - 100px);
	height: 300px;
	width: 385px;
	padding: 20px;
	z-index: 2;
}

.login input[type=text]{
	width: 250px;
	height: 30px;
	background: transparent;
	border: 1px solid rgba(255,255,255,0.6);
	border-radius: 2px;
	color: #fff;
	font-family: 'Exo', sans-serif;
	font-size: 16px;
	font-weight: 400;
	padding: 4px;
}

.login input[type=text]{
	width: 250px;
	height: 30px;
	background: transparent;
	border: 1px solid rgba(255,255,255,0.6);
	border-radius: 2px;
	color: #fff;
	font-family: 'Exo', sans-serif;
	font-size: 16px;
	font-weight: 400;
	padding: 4px;
	margin-top: 10px;
}

.login input[type=button]{
	width: 260px;
	height: 35px;
	background: #fff;
	border: 1px solid #fff;
	cursor: pointer;
	border-radius: 2px;
	color: #a18d6c;
	font-family: 'Exo', sans-serif;
	font-size: 16px;
	font-weight: 400;
	padding: 6px;
	margin-top: 10px;
}

.login input[type=button]:hover{
	opacity: 0.8;
}

.login input[type=button]:active{
	opacity: 0.6;
}

.login input[type=text]:focus{
	outline: none;
	border: 1px solid rgba(255,255,255,0.9);
}

.login input[type=password]:focus{
	outline: none;
	border: 1px solid rgba(255,255,255,0.9);
}

.login input[type=button]:focus{
	outline: none;
}


